<template>
<!--  -->
    <header v-bind:class="{fix:isFix}">
        <!--  -->
        <div class="left-menu">
            <!--  -->
            <div class="left-menu-content" v-bind:class="{ aniIn:isIn , aniOut:isOut}">
                <div class="line"></div>
                <!-- menu -->
                <li v-for="(item, index) in menus" :key="index" class="left-menu-child" v-on:click="scrollToId(index)">
                    <a>{{item}}</a>
                </li>
            </div>
            <!--  -->
            <img class="menu-logo" src="../assets/list.png" v-on:click="openMenu()">
        </div>
        <!--  -->
        <!-- logo  -->
        <div class="logo-container"> 
            <router-link :to="'home'"><img class="logo" src="../assets/self-logo.png"></router-link>
        </div>
        <!-- logo  -->
        <!-- menu -->
        <div class="menu">
            <!-- menu -->
            <ul class="menu-title">
                <li v-for="(item, index) in menus" :key="index" 
                v-on:mouseover="currentMenu(index)">
                    <a v-on:click="scrollToId(index)">{{item}}</a>
                    <img class="imgMenu" src="../assets/logo.png">
                    <!-- menu -->
                    <ul class="menu-child">
                        <div>
                        <li v-for="(item, index) in menuChilds" :key="index">
                            <a class="child">{{item}}</a>
                        </li>
                        </div>
                    </ul>
                </li>
            </ul>
        </div>
        <!-- menu -->
    </header>
</template>

<script>

export default {
    data () {
        return {
            menus:this.menu.title,//main menu
            connectHref:["/Android","/web","/work","/View","/educate"],
            totalMenu:[
                ["","",""],
                ["","",""],
                ["","",""],
                ["","",""],
                ["","",""]
            ],//total child menu
            scrollId:this.menu.link,
            menuChilds:[],//current child menu
            isFix:true,//header
            isIn:false,//menu
            isOut:false,//menu
        }
    },
    props:['menu'],
    mounted () {
        // this.handleScroll()   header 
        // window.addEventListener('scroll',this.handleScroll)

        //   
        document.addEventListener("click",(e)=> {
            // menu-logo menu
            let flag = e.target.contains(document.getElementsByClassName('menu-logo')[0]);
            if(flag){return;}
            else{ if(this.isIn == true){ this.closeMenu() } }  
        })
        window.addEventListener("resize",()=>{
            this.isIn = false;
            this.isOut = false;
        })
    },
    methods: {
        // Header 
        handleScroll(){
            // if(window.pageYOffset > 0){this.isFix = true;}
            // else{this.isFix = false;}
        },
        // menuchild menu
        currentMenu(index){
            this.menuChilds = this.totalMenu[index]
        },
        scrollToId(index){
            this.closeMenu();  
            const yourElement = document.getElementById(this.scrollId[index]);
            const yCoordinate = yourElement.getBoundingClientRect().top + window.pageYOffset;
            const yOffset = -60; 

            window.scrollTo({
                top: yCoordinate + yOffset,
                behavior: 'smooth'
            });
            // window.scrollTo( 0, document.body.scrollHeight/this.scrollId[index] ); 
            // window.scrollTo({
            //     top: document.body.scrollHeight/this.scrollId[index], 
            //     behavior: "smooth" 
            // });
        },
        setHref(index){
            return this.connectHref[index]
        },
        // menu-logo Menu
        openMenu(){
            if(this.isIn == true){this.closeMenu();}
            else{
                this.isOut = false;
                this.isIn = true;
            }
        },
        // Menu menuaniOut remove 
        closeMenu(){
            this.isOut = true;
            this.isIn = false;
            setTimeout(()=>{ this.isOut = false;},500); 
            clearTimeout(0); 
        },
    },
}
</script>

<style scoped>
/*  */
li{
    list-style-type: none;
}
/*  header */
.fix{
    width: 100vw;
    position: fixed;
}
/*   */
.left-menu{
    display: none;
    position: relative;
    flex-grow: 1;
    align-items: center;
    padding-left: 10px;
}
/* header */
.line{
    width: 100%;
    height: 1.5px;
    background: #d3d3d3;
    z-index: 1;
    margin-bottom: 2vw;
}
/*  headabsolute z-index1 */
.left-menu-content{
    display: none;
    flex-direction: column;
    position: absolute;
    width: 35vw;
    height: 100vh;
    top: 4.8vw;
    background: white;
    z-index: 1;
}
/*  */
.aniIn{
    display: flex;
    left:0vw;
    animation: left-in;
    animation-duration: 0.5s; 
}
/*  */
.aniOut{
    display: flex;
    left:-35vw;
    animation: left-out;
    animation-duration: 0.5s; 
}
/*  */
.left-menu-child{
    display: flex;
    align-items: center;
    padding-left: 3.5vw;
    height: 4vw;
    width: 100%;
    cursor: pointer;
}
.left-menu-child > a{
    font-size: 1.5vw;
}
/*  */
.left-menu-child:hover{
    background: #90ee90;
}
/*  */
.left-menu-child:hover > a{
    color: white;
}
/* left-menu */
.menu-logo{
    width: 3.5vw;
    padding-left: 10px;
    cursor: pointer;
}
/* header 
    flex growtitle 5 
    align-items justify-content 
*/
.logo-container{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-grow: 6;
    color: black;
}
/* self logo */
.logo{
    width: 10vw;
    padding: 15px;
    margin-left: 2vw;
}
/* menu
    flex growmenu 11 
    align-items 
 */
.menu{
  display: flex;
  align-items: center;
  flex-grow: 10;
  color: black;
}
/* ul  li  display*/ 
.menu-title{
    height: 100%;
    display: flex;
    /* align-items: center; */
    justify-content: space-around;
}
/* position relative ul  align-items: center 
menutopheader */
ul.menu-title > li{
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    float: left;
    margin-right: 3vw;
    font-weight: bold;
    white-space: nowrap; /*  li ul*/
}
ul.menu-title a:hover {
    color: #90ee90;
}
/* menu */
.imgMenu{
    width: 15px;
    height: 15px;
    margin-left: 5px;
}
/* position absolute li */
ul.menu-title li > ul{
    display: none;
    position: absolute;
    z-index: 1;
    /* float: left; */
}
/*  */
/* menu  displayflex column */
/* ul.menu-title li:hover > ul{
    display: flex;
    flex-direction: column;
    top: 100%;
    width: 15vw;
} */
/* (a)displayblock pointer */
.child{
    display: block;
    cursor: pointer;
    -webkit-user-select: none;
    padding: 20px;
    background: #f8f8ff;
}

/*  */
@media all and (max-width: 1080px){
    /* menu */
    .menu{
        display: none;
    }
    /* title */
    .logo-container{
        flex-grow: 12;
        padding-right: 10%;
    }
    /* logo */
    .logo{
        padding: 0;
        width: 10vw;
        height: 7vw;
    }
    /* menu */
    .left-menu{
        display: flex;
    }
    .left-menu-child > a{
        font-size: 2.2vw;
    } 
}
/*  */
@media all and (max-width: 880px){
    .menu-logo{
        width: 4.5vw;
    }
    .logo{
        padding-top: 1vw;
        width: 13vw;
        height: 10vw;
    }
    .left-menu-child > a{
        font-size: 3vw;
    }
    .left-menu-content{
        top: 6.5vw;
        width: 50vw;
    }
    .left-menu-child{
        margin-top: 2vw;
    }
    /*  */
    .aniIn{
        left:0vw;
        animation: big-left-in;
        animation-duration: 0.5s; 
    }
/*  */
    .aniOut{
        display: flex;
        left:-50vw;
        animation: big-left-out;
        animation-duration: 0.5s; 
    }   
}
@media all and (max-width: 700px){
    .menu-logo{
        width: 5.5vw;
    }
      
}
</style>

