<template>
<!--  -->
    <div v-bind:id="content.type" class="content-body">
        <!--   -->
        <div class="full-screen" v-if="showFull" v-on:click="hideFullImg()" v-bind:style="setTop">
            <img v-bind:src="fullImg()" >
        </div>
        <!--  -->
        <div class="imgBody" v-for="(item, index) in src" :key="index"  v-bind:class="setMargin(index)">
            <!--  -->
            <div><img src="../../assets/add.png" v-on:click="showFullImg(index)"></div>
            <img v-bind:src= item>
        </div>
    </div>
</template>


<script>

import html from '../../assets/html.png'
import css from '../../assets/css.png'
import js from '../../assets/js.png'
import vue from '../../assets/logo.png'
import android from '../../assets/android-studio.png'
import c_language from '../../assets/c-language.png'
import c_sharp from '../../assets/c-sharp.png'
import java from '../../assets/java.png'

export default {
    data () {
        return {
            src:[html,css,js,vue,java,android,c_language,c_sharp],//
            showFull:false,//
            clickImg:html,//
            marginIndex:[0,4],//marginindex
        }
    },
    props: ['content'],
    mounted(){
        // 
        window.addEventListener("resize",this.handleResize)
    },
    methods: {
        //  margin
        handleResize(){
            if(window.innerWidth < 940){this.marginIndex = [0,2,4,6]}
            else{this.marginIndex = [0,4]}
        },
        //  marginLeft
        setMargin(index){
            if(!this.marginIndex.includes(index)){return "imgBody-margin"}
            else{return ""}
        },
        // 
        showFullImg(index){
            this.clickImg = this.src[index];
            this.showFull = true;
            var mo=function(e){e.preventDefault();};//??
            document.body.style.overflow='hidden';//
            document.addEventListener("touchmove",mo,false);//
        },
        // 
        hideFullImg(){
            this.showFull = false;
            var mo=function(e){e.preventDefault();};//??
            document.body.style.overflow='';//
            document.removeEventListener("touchmove",mo,false);//
        },
        // 
        fullImg(){
            return this.clickImg;
        }
    },
    computed: {
        // 
        setTop(){
            if(this.showFull == true){return{top: window.pageYOffset + 'px'}}
            else{return{top: 0}}
        },
    },
}
</script>


<style scoped>
/*    */
.content-body{
    display: flex;
    flex-wrap: wrap;
    min-height: 45vw;
    background: #fff;
    color: #2c3e50;
    margin-left: 4vw;
    margin-right: 4vw;
}
/*   z-index */
.full-screen{
    position: absolute;
    left: 0;
    height: 100vh;
    width: 100vw;
  
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 2;
  
    display: flex;
    justify-content: center;
    align-items: center;
}
/*  */
.full-screen > img{
    height: 30vw;
    width: 35vw;
}
/*    */
.imgBody{
    display: flex;
    margin-bottom: 1%;
    flex-basis: 23.25%;
    justify-content: center;
    align-items: center;
    background: #f8f8ff;
    position: relative;
}
/*  */
.imgBody-margin{
    margin-left: 2%;
}
/*  */
img{
    width: 66%;
    height: 60%;
}
/*  position absolute  */
.imgBody > div {
    position: absolute;
    display: none;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
}
/*  */
.imgBody:hover > div{
    display: flex;
    background: #00fa9a;
    opacity: 0.8;
    animation: fade-in;/**/
    animation-duration: 0.5s;
}
/*   */
.imgBody > div > img{
    width: 20%;
    height: 20%;
    cursor: pointer;
    animation: bump-in;/**/
    animation-duration: 0.5s;
}


/* RWD  */
@media all and (max-width: 940px){
    .imgBody{
        flex-basis: 48%;
        height: 33vw;
    }
    img{
        width: 63%;
        height: 73%;
    }
}
</style>
