<template>
  <div id="slider">
    <!--  -->
    <div class="window" @mouseover="stop" @mouseleave="play">
      <!--  -->
      <ul class="container" :style="containerStyle">
        <!--  -->
        <li class="content">  
          <img :src="sliders[sliders.length - 1].img" alt="">
        </li>
        <!--  -->
        <li class="content" v-for="(item, index) in sliders" :key="index">  
          <img :src="item.img" alt="">
        </li>
        <!--  -->
        <li class="content">  
          <img :src="sliders[0].img" alt="">
        </li>
      </ul>
      <!--  -->
      <ul class="direction">  
        <li class="left" @click="move(contentWidth,1,speed)">
          <svg class="left-icon" width="2.5vw" height="2.5vw" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg"><path fill="#ffffff" d="M481.233 904c8.189 0 16.379-3.124 22.628-9.372 12.496-12.497 12.496-32.759 0-45.256L166.488 512l337.373-337.373c12.496-12.497 12.496-32.758 0-45.255-12.498-12.497-32.758-12.497-45.256 0l-360 360c-12.496 12.497-12.496 32.758 0 45.255l360 360c6.249 6.249 14.439 9.373 22.628 9.373z"  /></svg>          
        </li>
        <li class="right" @click="move(contentWidth,-1,speed)">
          <svg class="right-icon" width="2.5vw" height="2.5vw" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg"><path fill="#ffffff" d="M557.179 904c-8.189 0-16.379-3.124-22.628-9.372-12.496-12.497-12.496-32.759 0-45.256L871.924 512 534.551 174.627c-12.496-12.497-12.496-32.758 0-45.255 12.498-12.497 32.758-12.497 45.256 0l360 360c12.496 12.497 12.496 32.758 0 45.255l-360 360c-6.249 6.249-14.439 9.373-22.628 9.373z"  /></svg>          
        </li>
      </ul>
      <!--  -->
      <ul class="dots"> 
        <li v-for="(dot, i) in sliders" :key="i" 
        :class="{dotted: i === (currentIndex-1)}"
        @click = jump(i+1)>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>

import bg from '../../assets/slide-bg.jpg'
import bg2 from '../../assets/slide-bg2.jpeg'
import bg3 from '../../assets/slide-bg3.jpg'
import bg4 from '../../assets/slide-bg4.jpg'
import bg5 from '../../assets/slide-bg5.jpg'

export default {
  name: 'slider',
  props: {
    initialSpeed: {
      type: Number,
      default: 40
    },
    initialInterval: {
      type: Number,
      default: 4
    }
  },
  data () {
    return {
      sliders:[{img:bg},{img:bg2},{img:bg3},{img:bg4},{img:bg5}], //
      currentIndex:1, //
      contentWidth:window.innerWidth, //
      distance:-(window.innerWidth), // 
      transitionEnd: true, // 
      speed: this.initialSpeed, //
    }
  },
  computed:{
    containerStyle() {
      return {
        transform:`translate3d(${this.distance}px, 0, 0)` //XYZ
      }
    },
    interval() {
      return this.initialInterval * 1000 //
    }
  },
  mounted() {
      this.init()
      this.distance = -(window.innerWidth); //()
      window.onresize = () =>{
        this.stop()//  
        this.distance = -(window.innerWidth) * this.currentIndex; //distance
        this.contentWidth = window.innerWidth //
      };
  },
  methods:{
      init(){
        this.play() //
        window.onblur = function() { this.stop() }.bind(this) //
        window.onfocus = function() { this.play() }.bind(this) //
       },
      move(offset,direction,speed) {
        if (!this.transitionEnd) return  //  
        this.transitionEnd = false // 
        //  jump
        direction === -1 ? this.currentIndex += offset/this.contentWidth : this.currentIndex -= offset/this.contentWidth
        if (this.currentIndex > 5) this.currentIndex = 1
        if (this.currentIndex < 1) this.currentIndex = 5

        const destination = this.distance + offset * direction
        this.animate(destination,direction,speed) //
      },
      animate(des, direc,speed) {
        //  this.temp ?
        if (this.temp) { 
          window.clearInterval(this.temp)
          this.temp = null 
        }
        this.temp = window.setInterval(() => {
          //10 speed
            if ((direc === -1 && des < this.distance - speed) || (direc === 1 && des > this.distance + speed)) {
                this.distance += speed * direc 
            } else {
              //    distance 
                this.transitionEnd = true
                window.clearInterval(this.temp)
                this.distance = des
                if (des < -(window.innerWidth * 5) ) this.distance = -(window.innerWidth)
                if (des > -(window.innerWidth)) this.distance = -(window.innerWidth * 5)
            }
        }, 10)
      },
      jump(index) {
        const direction = index - this.currentIndex >= 0 ? -1 : 1 //  -1 1 
        const offset = Math.abs(index - this.currentIndex) * window.innerWidth //
        // 
        const jumpSpeed = Math.abs(index - this.currentIndex) === 0 ? this.speed : Math.abs(index - this.currentIndex) * this.speed 
        this.move(offset, direction, jumpSpeed)
      },
      play() {
        // intervalclear
        if (this.timer) {
            window.clearInterval(this.timer)
            this.timer = null
        }
        this.timer = window.setInterval(() => {
            this.move(this.contentWidth, -1, this.speed)
        }, this.interval)
      },
      stop() {
        window.clearInterval(this.timer)
        this.timer = null
      }
    }
}
</script>

<style scoped>
ol,ul{
  list-style: none;
}
#slider{
  /* display: flex; */
  text-align: center;
}
/* overflow:hiden  position :relative container*/
.window{
  position:relative;
  flex-basis: 100vw;
  height: 95vh;
  max-height: 47vw;
  margin:0 auto;
  overflow:hidden;
}
/*   position :absolute window*/
.container{
  display:flex;
  /* flex-basis: 100%; */
  /* height: 50vw; */
  position:absolute;
}
img{
  user-select: none;
  height: 48vw;
  width: 100vw;
}
/* .content{
  flex-basis: 100%;
  height: 50vw;
} */

/*  */
.left, .right{
  position:absolute;
  top:50%;
  transform:translateY(-50%);
  width:3.5vw;
  height:3.5vw;
  background-color:rgba(0,0,0,.3);
  border-radius:50%;
  cursor:pointer;
}
/*  */
.left{
  left:3%;
  padding-left:12px;
  padding-top:10px;
}
.left-icon{
  position:absolute;
  top:20%;
  left:25%;
}
/*  */
.right{
  right:3%;
  padding-right:12px;
  padding-top:10px;
}
.right-icon{
  position:absolute;
  top:20%;
  right:25%;
}
/*  */
.dots{
    position:absolute;
    bottom:10%;
    left:50%;
    transform:translateX(-50%);
  }
  /*  */
.dots li{
  display:inline-block;
  width:15px;
  height:15px;
  margin:0 3px;
  border:1px solid white;
  border-radius:50%;
  background-color:#333;
  cursor:pointer;
}
/* currentIndex */
.dots .dotted{
  background-color:orange;
}
/*  */
@media all and (max-width: 1080px){
    .left-icon{
      width: 4vw;
      height: 4vw;
      top:15%;
    }
    .right-icon{
      width: 4vw;
      height: 4vw;
      top:15%;
    }
    .left{
      width: 5.2vw;
      height: 5.2vw;
    }
    .right{
      width: 5.2vw;
      height: 5.2vw;
    }
}
</style>