<template>
<!-- content.type  -->
    <div v-bind:id="content.type" class="content-body" v-bind:style="setHeight()">
        <!-- title -->
        <div class="title-body">
            <p class="title">{{content.title}}</p>
            <p class="content" v-html="content.content"></p>
        </div>
        <!--    content.type self -->
        <div class="self" v-if="content.type === 'self'">
            <!-- self -->
            <div class="self-child" v-for="(item, index) in selfList" :key="index">
                <!--  -->
                <img class="self-img" v-bind:src="imgSrc(index)">
                <!-- <a class="self-title" v-bind:href="item.link">{{item.title}}</a> -->
                <router-link class="self-title" :to= item.link>{{item.title}}</router-link>
                <p class="self-text">{{item.content}}</p>
                <!-- hover -->
                <div class="connectParent">
                    <div class="connect">
                        <img v-for="(item, index) in connectList" :key="index" 
                        v-bind:src="connectImg(index)"
                        v-on:click="showMsg(index)">
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>

import web from '../../assets/web.png'
import educate from '../../assets/educate.png'
import work from '../../assets/work.png'

import fb from '../../assets/facebook.png'
import ig from '../../assets/instagram.png'
import youtube from '../../assets/youtube.png'
import google from '../../assets/google-plus.png'
import line from '../../assets/line.png'

export default {
    data () {
        return {
            // self
            selfList:[
                {title:'John',content:"web",img:web,link:"/web"},
                {title:"James",content:"educate",img:educate,link:"/web"},
                {title:"Andy",content:"work",img:work,link:"/web"}
            ],
            // self
            styleObject:{
                minHeight: '45vw',
                paddingTop: '2vw',
            },
            // self hover
            connectList:[
                {title:'fb',img:fb},
                {title:'ig',img:ig},
                {title:'google',img:google},
                {title:'youtube',img:youtube},
                {title:'line',img:line}
            ],
        }
    },
    props: ['content'],
    methods: {
        // self  day content-body
        setHeight(){
            if(this.content.type === "self"){
                return this.styleObject
            }    
        },
        // self
        imgSrc(index){
            return this.selfList[index].img;
        },
        // self hoverfb ig 
        connectImg(index){
            return this.connectList[index].img
        },
        // fb
        showMsg(index){
            alert(this.connectList[index].title)
        },
    }
}
</script>

<style scoped>
/*  35 */
.content-body{
    display: flex;
    flex-direction: column;
    min-height: 35vw;
    background: #fff;
    color: #2c3e50;
}
/* title body  &&  */
.title-body{
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    align-items: center;
}
/*   */
.title{
    display: flex;
    align-items: center;
    font-size: 3.5vw;
    font-weight: bold;
    font-family: 'Literata' ,'serif';
    flex-grow: 1;
}
/*   */
.content{
    display: flex;
    flex-grow: 1;
    text-align: center;
    font-size: 1.3vw;
    font-family: 'Noto Sans HK', sans-serif; 
    color: #a9a9a9;
}
/*   3 &&  */
.self{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    flex-grow: 3;
    background: #fff;
    margin: 2vw;
}
/*    positionconnectParent*/
.self > div{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 27vw;
    margin: 2vw;
    padding: 3vw;
    position: relative;
}
/*  1 */
.self-img{
    flex-grow: 1;
    width: 12.5vw;
    height: 10vw;
}
/*    */
.self-title{
    display: flex;
    flex-grow: 1;
    align-items: center;
    text-align: center;
    line-height: 2.5vw;
    font-size: 2vw;
    font-family: 'Literata' ,'serif';
}
.self-text{
    font-size: 1.2vw;
}
.self-title:hover{
    color:#90ee90;
    cursor: pointer;
    animation: color-green-out;
    animation-duration: 0.5s;
}
.self-title:hover .connect{
    opacity:0.8;
    transform: translateZ(30px) rotateY(360deg) scale(1.1);
    animation: fade-in;
    animation-duration: 0.3s;
}
/*  */
.connectParent{
    position: absolute;
    width: 60%;
    height: 50%;
    top: 11%;
}
/*  */
.connect{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    background: #dcdcdc;
    opacity:0;
    transform-style: preserve-3d;
    transition: 0.5s;
}
/*  */
/* hover  */
.connectParent:hover .connect{
    opacity:0.8;
    transform: translateZ(30px) rotateY(360deg) scale(1.1);
    animation: fade-in;
    animation-duration: 0.3s;
}
/* style */
.connect > img{
    width: 10%;
    height: 12%;
    margin-right: 10px;
}
/* hover  */
.connect > img:hover{
    width: 13%;
    height: 13%;
}
/* 1240px */
@media all and (max-width: 1240px){
    .title{
        font-size: 5vw;
    }
    .content{
        font-size: 2vw;
    }
    .self > div{
        width: 40vw;
        margin: 0;
        padding: 1vw;
    }
    .self-title{
        font-size: 2.5vw;
        margin-bottom: 10px;
    }
    .self-img{
        width: 18vw;
        height: 18vw;
        margin: 2vw;
    }
    .self-text{
        font-size: 1.8vw;
    }
    .connectParent{
        top: 12%;
        width: 60%;
        height: 60%;
    }
}

</style>
