<template>
<!--  -->
    <div v-bind:id="content.type" class="content-body" v-bind:class="setHeightLeft()">  
        <!--  order -->
        <div v-bind:class="leftOrRight()" >
            <p class="title">{{content.title}}</p>
            <p class="content" v-html="content.content"></p>
        </div>
        <!--  -->
        <div class="content-img">
            <!-- padding -->
            <img v-bind:src="setSrc()" v-bind:class="setPad()"> 
        </div>
    </div>
</template>


<script>


export default {
    data () {
        return {

        }
    },
    props: ['content'],
    methods: {
        // minheight  
        setHeightLeft(){
            if(this.content.type == "left"){return 'style-hight-left'}
        },
        // padding 
        setPad(){
            if(this.content.type == "left"){return 'style-left'}
            else{return 'style-right'}
        },
        //  style
        leftOrRight(){
            if(this.content.type === "left"){return 'left-content';}
            else{return 'right-content';}
        },
        // 
        setSrc(){
            if(this.content.type === "right"){return require("../../assets/intro-vue.jpg");}
            else if(this.content.type === "left"){return require("../../assets/android.png");}
        }
    }
}
</script>

<style scoped>
/*  */
.content-body{
    display: flex;
    min-height: 30vw;
    margin-left: 4vw;
    margin-right: 4vw;
}
/*  */
/* .style-hight-left{
    min-height: 30vw;
} */
/*     */
.right-content{
    display: flex;
    flex-grow: 1;
    flex-direction: column;
    text-align: center;
    align-items: center;
    color: black;
    background: #f8f8ff;
}
/*      */
.left-content{
    order: 1;
    display: flex;
    flex-grow: 1;
    flex-direction: column;
    text-align: center;
    align-items: center;
    color: black;
    background: #f8f8ff;
}
/*    */
.title{
    display: flex;
    flex-grow: 1;
    align-items: center;
    font-size: 2.3vw;
    font-weight: bold;
    font-family: 'Literata' ,'serif';
    padding-top: 15px;
}
/*  */
.content{
    flex-grow: 1;
    font-size: 1.3vw;
    font-family: 'Noto Sans HK', sans-serif; 
    color: #a9a9a9;
}
/*   */
.content-img{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50%;
}
/*  */
.style-left{
    width: 100%;
    height: 100%;
}
/*  */
.style-right{
    width: 100%;
    height: 100%;
}

/* RWD  */
@media all and (max-width: 1240px){
    .content-body{
        flex-direction: column;
        margin: 0;
        min-height: 60vw;
        background: #f8f8ff;
    }
    /* .style-hight-left{
        min-height: 60vw;
    } */
    .left-content{
        order: 0;
    }
    .title{
        font-size: 2.9vw;
    }
    .content{
        font-size: 1.9vw;
    }
    .content-img{
        width: 100%;
        background: #f8f8ff;
    }
    .style-left{
        height: 35vw;
        width: 60vw;
        display: block;
        padding: 0;
    }
    .style-right{
        height: 35vw;
        width: 60vw;
        display: block;
        padding: 0;
    }
}
</style>

